import React,{Component} from 'react'
import { hashHistory } from 'react-router'
import { Form, Button, message, Radio, Spin, Input, Select, Cascader } from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;
const { TextArea } = Input;
import InvestItem from '../FormItem/investItem.jsx'

export class TransferForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            disabled: props.actionType == "detail" ? true : false,
            loading: false,
            "assetsType": {
                "debts":[],
                "stock": [],
                "purchase": [],
                "transfer": []
            },
            "industryList": [],
            "cascaderRegion": [],
            "project": {   //项目
                "key": "", // 编号
                "title": "", //标题
                "category": "", //类别
                "type": "", //类型
                "detail": "",   //详情
                "accessory": {        //附件
                    "name": "",
                    "link": ""
                }
            },
            "financing": {  //融资方
                "industry": "", //行业
                "aptitude": "", //资质
                "parentAptitued": "", //母公司资质
                "region": [], //地区
                "previousProfits": "", //去年净利润
                "recentProfits": "",
                "nextProfits": "",
                "number": "", //金额
                "deadline": "", //期限
                "maxRate": "" //最高可接受利率
            },
            "assets": {
                "region": ["0","1","2"],
                "value": 214,
                "number": 2145
            },
            "mortgage": {       //抵押物
                "isNeed": "",    //是否有抵押物
                "type": [],  //抵押物类型
                "name": "",  //抵押物名称
                "value": "",  //抵押物价值
                "rate": "" //抵押物折扣率
            },
            "guarantee": {
                "isNeed": "",    //是否必须担保
                "method": ""   //担保方式
            },
            "stock": {
                "type": "",  //参股性:1   控股型:2
                "property": "",  //老股转让:1   新股增发:2
                "number": "",  //转让金额
                "rate": "", //出让股权比例
                "estimate": {  //估值
                    "times": "",
                    "type": ""
                }
            },
            "target": {  //标的企业
                "name": "",
                "industry": "",
                "region": "",
                "value": "", //企业整体估值
                "previousProfits": "",
                "recentProfits": "",
                "nextProfits": ""
            }
        };
        ['handleSubmit', 'resetForm'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        let that = this;
        this.setState({loading: true});
        ajax({
            url: "/delegateProject/getInfoDetail",
            method: "post",
            data: {id: that.props.infoID ? that.props.infoID : ""},
            callback: function(data) {
                if(data.status == 200) {
                    let newData = data.data;
                    that.setState(Object.assign({loading: false}, newData));
                }
            }
        })
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if(!err) {
                ajax({
                    url: "/delegateProject/audit",
                    method: "post",
                    data: values,
                    callback: function(data) {
                        if(data.status == 200) {
                            message.success("保存成功!", 1, function() {
                                hashHistory.push("/delegateProject")
                            });
                        } else {
                            message.error(data.errMsg, 1)
                        }
                    }
                });
            }
        })
    }

    return() {
        history.back();
    }

    resetForm() {
        this.props.form.resetFields();
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 3 },
            wrapperCol: { span: 14 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="项目标题"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("project.title", {
                            rules: [{require: true, message: "请输入标题!"}],
                            initialValue: this.state.project.title
                        })(
                            <Input disabled={this.state.disabled} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="项目类别"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("project.category", {
                            initialValue: "4"
                        })(
                            <Select disabled>
                                <Option value="1">债权</Option>
                                <Option value="2">股权</Option>
                                <Option value="4">资金转让</Option>
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="项目类型"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("project.type", {
                            rules: [{
                                required: true,
                                message: "请选择项目类型!"
                            }],
                            initialValue: this.state.project.type
                        })(
                            <Select disabled={this.state.disabled}>
                                {
                                    this.state.assetsType.transfer.map(function(item) {
                                        return <Option value={item.key} key={item.key}>{item.value}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="资产转让人"
                    >
                        <div>详见我的联系方式</div>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="资产所在地"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("assets.region", {
                            rules: [{
                                required: true,
                                message: "请选择资产所在地!"
                            }],
                            initialValue: this.state.assets.region
                        })(
                            <Cascader disabled={this.state.disabled}
                                      options={this.state.cascaderRegion} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="转让金额"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("assets.number", {
                            rules: [{
                                required: true,
                                message: "请输入转让金额!"
                            }],
                            initialValue: this.state.assets.number
                        })(
                            <Input disabled={this.state.disabled} style={{width: "93%"}} />
                        )}
                        <span className="ant-form-text" style={{marginLeft: 7}}>万元</span>

                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="资产价值"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("assets.value", {
                            rules: [{
                                required: true,
                                message: "请输入资产价值!"
                            }],
                            initialValue: this.state.assets.value
                        })(
                            <Input disabled={this.state.disabled} style={{width: "93%"}} />
                        )}
                        <span className="ant-form-text" style={{marginLeft: 7}}>万元</span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="项目详情"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("project.detail", {
                            rules: [{require: true, message: "请输入项目详情!"}],
                            initialValue: this.state.project.detail
                        })(
                            <TextArea disabled={this.state.disabled} autosize={{ minRows: 3 }} />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="附件"
                    >
                        <span className="ant-form-text"><a href={this.state.project.accessory.link}>{this.state.project.accessory.name}</a>
                        </span>
                    </FormItem>
                    <FormItem wrapperCol={{ span: 8, offset: 3 }}>
                        {
                            !this.state.disabled ? <Button type="primary" onClick={this.handleSubmit} style={{"marginRight": "20px"}}>立即发布</Button> : ""
                        }
                        {
                            !this.state.disabled ? <Button type="danger" onClick={this.resetForm} style={{"marginRight": "20px"}}>重置</Button> : ""
                        }
                        <Button icon="rollback" onClick={this.return}>返回</Button>
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedTransferForm = Form.create({})(TransferForm);

module.exports = WrappedTransferForm;


