import React,{Component} from 'react'
import { hashHistory } from 'react-router'
import { Form, Button, message, Radio, Spin, Input, Select, Cascader, Checkbox } from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;
const CheckboxGroup = Checkbox.Group;
const { TextArea } = Input;
export class DebtsForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            disabled: props.actionType == "detail" ? true : false,
            loading: false,
            "assetsType": {
                "debts":[],
                "stock": [],
                "purchase": [],
                "transfer": []
            },
            "industryList": [],
            "cascaderRegion": [],
            "project": {   //项目
                "key": "", // 编号
                "title": "", //标题
                "category": "", //类别
                "type": "", //类型
                "detail": "",   //详情
                "accessory": {        //附件
                    "name": "",
                    "link": ""
                }
            },
            "financing": {  //融资方
                "industry": "", //行业
                "aptitude": "", //资质
                "parentAptitue": "", //母公司资质
                "region": [], //地区
                "previousProfits": "", //去年净利润
                "recentProfits": "",
                "nextProfits": "",
                "number": "", //金额
                "deadline": "", //期限
                "maxRate": "" //最高可接受利率
            },
            "mortgage": {       //抵押物
                "isNeed": "",    //是否有抵押物
                "type": [],  //抵押物类型
                "name": "",  //抵押物名称
                "value": "",  //抵押物价值
                "rate": "" //抵押物折扣率
            },
            "guarantee": {
                "isNeed": "",    //是否必须担保
                "method": ""   //担保方式
            },
            "stock": {
                "type": "",  //参股性:1   控股型:2
                "property": "",  //老股转让:1   新股增发:2
                "number": "",  //转让金额
                "rate": "", //出让股权比例
                "value": "", //企业整体估值
                "estimate": {  //估值
                    "times": "",
                    "type": ""
                }
            },
            "subject": {  //标的企业
                "name": "",
                "industry": "",
                "region": "",
                "previousProfits": "",
                "recentProfits": "",
                "nextProfits": ""
            },
            "assets": { //资产
                "region": "",
                "number": "",
                "value": ""
            },
            "audit": {
                "status": "",
                "comment": ""
            }
        };
        ['handleSubmit', 'resetForm', 'financingIndustryChange', 'mortgageChange', 'guaranteeChange'].forEach(m=>this[m] = this[m].bind(this))
    }

    guaranteeChange(e) {
        this.setState({
            guarantee: {
                isNeed: e.target.value
            }
        })
    }

    mortgageChange(e) {
        this.setState({
            mortgage: {
                isNeed: e.target.value
            }
        })
    }

    financingIndustryChange(e) {
        let value = e.target.value;
        //房地产
        if(value == "1") {
            this.setState({
                financing: {
                    industry: value
                }
            })
        }
    }

    componentWillMount() {
        let that = this;
        this.setState({loading: true});
        ajax({
            url: "/delegateProject/getInfoDetail",
            method: "post",
            data: {id: that.props.infoID ? that.props.infoID : ""},
            callback: function(data) {
                if(data.status == 200) {
                    let newData = data.data;
                    that.setState(Object.assign({loading: false}, newData));
                }
            }
        })
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if(!err) {
                ajax({
                    url: "/delegateProject/edit",
                    method: "post",
                    data: values,
                    callback: function(data) {
                        if(data.status == 200) {
                            message.success("保存成功!", 1, function() {
                                hashHistory.push("/delegateProject")
                            });
                        } else {
                            message.error(data.errMsg, 1)
                        }
                    }
                });
            }
        })
    }

    return() {
        history.back();
    }

    resetForm() {
        this.props.form.resetFields();
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 3 },
            wrapperCol: { span: 14 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="项目标题"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("project.title", {
                            rules: [{required: true, message: "请输入标题!"}],
                            initialValue: this.state.project.title
                        })(
                            <Input disabled={this.state.disabled} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="项目类别"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("category", {
                            initialValue: "1"
                        })(
                            <Select disabled>
                                <Option value="1">债权</Option>
                                <Option value="2">股权</Option>
                                <Option value="4">资金转让</Option>
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="项目类型"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("project.type", {
                            rules: [{
                                required: true,
                                message: "请选择项目类型!"
                            }],
                            initialValue: this.state.project.type
                        })(
                            <Select disabled={this.state.disabled}>
                                {
                                    this.state.assetsType.debts.map(function(item) {
                                        return <Option value={item.key} key={item.key}>{item.value}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="融资方名称"
                    >
                        <div>详见我的联系方式</div>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="融资方所属行业"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("industry", {
                            rules: [{
                                required: true,
                                message: "请选择所属行业!"
                            }],
                            initialValue: this.state.financing.industry
                        })(
                            <Select onChange={this.financingIndustryChange} disabled={this.state.disabled}>
                                {
                                    this.state.industryList.map(function(item) {
                                        return <Option value={item.key} key={item.key}>{item.value}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </FormItem>
                    {
                        this.state.financing.industry == 1 ?
                            <FormItem
                                required
                                {...formItemLayout}
                                label="融资方开发资质"
                                hasFeedback={!this.state.disabled}
                            >
                                {getFieldDecorator("financing.aptitude", {
                                    rules: [{
                                        required: true,
                                        message: "请输入融资方开发资质!"
                                    }],
                                    initialValue: this.state.financing.aptitude
                                })(
                                    <Input disabled={this.state.disabled} />
                                )}
                            </FormItem> : ""
                    }
                    {
                        this.state.financing.industry == 1 ?
                            <FormItem
                                required
                                {...formItemLayout}
                                label="母公司开发资质"
                                hasFeedback={!this.state.disabled}
                            >
                                {getFieldDecorator("financing.parentAptitue", {
                                    rules: [{
                                        required: true,
                                        message: "请输入母公司开发资质!"
                                    }],
                                    initialValue: this.state.financing.parentAptitue
                                })(
                                    <Input disabled={this.state.disabled} />
                                )}
                            </FormItem> : ""
                    }
                    <FormItem
                        required
                        {...formItemLayout}
                        label="融资方所在地"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("financing.region", {
                            rules: [{
                                required: true,
                                message: "请设置地区!"
                            }],
                            initialValue: this.state.financing.region
                        })(
                            <Cascader disabled={this.state.disabled} options={this.state.cascaderRegion} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="融资方去年净利润"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("financing.previousProfits", {
                            rules: [{
                                required: true,
                                message: "请输入去年净利润!"
                            }],
                            initialValue: this.state.financing.previousProfits
                        })(
                            <Input style={{width: "93%"}} disabled={this.state.disabled} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="融资金额"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("financing.number", {
                            rules: [{
                                required: true,
                                message: "请输入融资金额!"
                            }],
                            initialValue: this.state.financing.number
                        })(
                            <Input style={{width: "93%"}} disabled={this.state.disabled} />
                        )}
                        <span className="ant-form-text" style={{marginLeft: 7}}>万元</span>

                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="期限"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("financing.deadline", {
                            rules: [{
                                required: true,
                                message: "请输入期限"
                            }],
                            initialValue: this.state.financing.deadline
                        })(
                            <Input style={{width: "93%"}} disabled={this.state.disabled} />
                        )}
                        <span className="ant-form-text" style={{marginLeft: 7}}>年</span>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="最高接受利率"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("financing.maxRate", {
                            rules: [{
                                required: true,
                                message: "请输入最高接受利率"
                            }],
                            initialValue: this.state.financing.maxRate
                        })(
                            <Input style={{width: "93%"}} disabled={this.state.disabled} />
                        )}
                        <span className="ant-form-text" style={{marginLeft: 7}}>%</span>

                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="有无抵/质押物"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("mortgage.isNeed", {
                            rules: [{
                                required: true,
                                message: "请设置是否需要抵押物!"
                            }],
                            initialValue: this.state.mortgage.isNeed
                        })(
                            <RadioGroup onChange={this.mortgageChange} disabled={this.state.disabled}>
                                <Radio value="1">是</Radio>
                                <Radio value="0">否</Radio>
                            </RadioGroup>
                        )}
                    </FormItem>
                    {
                        this.state.mortgage.isNeed == "1" ?
                            <FormItem
                                {...formItemLayout}
                                label="抵/质押物类型"
                            >
                                {getFieldDecorator("mortgage.type", {
                                    initialValue: this.state.mortgage.type
                                })(
                                    <CheckboxGroup disabled={this.state.disabled}>
                                        <Checkbox value="1">固定资产</Checkbox>
                                        <Checkbox value="2">有价证券</Checkbox>
                                        <Checkbox value="3">其他</Checkbox>
                                    </CheckboxGroup>
                                )}
                            </FormItem> : ""
                    }
                    {
                        this.state.mortgage.isNeed == "1" ?
                            <FormItem
                                {...formItemLayout}
                                label="抵/质押物名称"
                            >
                                {getFieldDecorator("mortgage.name", {
                                    initialValue: this.state.mortgage.name
                                })(
                                    <Input disabled={this.state.disabled} />
                                )}
                            </FormItem> : ""
                    }
                    {
                        this.state.mortgage.isNeed == "1" ?
                            <FormItem
                                {...formItemLayout}
                                label="抵/质押物价值"
                            >
                                {getFieldDecorator("mortgage.value", {
                                    initialValue: this.state.mortgage.value
                                })(
                                    <Input style={{width: "93%"}} disabled={this.state.disabled} />
                                )}
                                <span className="ant-form-text" style={{marginLeft: 7}}>万元</span>
                            </FormItem> : ""
                    }
                    {
                        this.state.mortgage.isNeed == "1" ?
                            <FormItem
                                {...formItemLayout}
                                label="抵/质押物折扣率"
                            >
                                {getFieldDecorator("mortgage.rate", {
                                    initialValue: this.state.mortgage.rate
                                })(
                                    <Input style={{width: "95%"}} disabled={this.state.disabled} />
                                )}
                                <span className="ant-form-text" style={{marginLeft: 7}}>%</span>
                            </FormItem> : ""
                    }
                    <FormItem
                        required
                        {...formItemLayout}
                        label="有无担保"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("guarantee.isNeed", {
                            rules: [{
                                required: true,
                                message: "请设置担保信息!"
                            }],
                            initialValue: this.state.guarantee.isNeed
                        })(
                            <RadioGroup onChange={this.guaranteeChange} disabled={this.state.disabled}>
                                <Radio value="1">是</Radio>
                                <Radio value="0">否</Radio>
                            </RadioGroup>
                        )}
                    </FormItem>
                    {
                        this.state.guarantee.isNeed == "1" ?
                            <FormItem
                                {...formItemLayout}
                                label="担保方式"
                            >
                                {getFieldDecorator("guarantee.method", {
                                    initialValue: this.state.guarantee.method
                                })(
                                    <Input disabled={this.state.disabled} />
                                )}
                            </FormItem> : ""
                    }
                    <FormItem
                        required
                        {...formItemLayout}
                        label="项目详情"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("project.detail", {
                            rules: [{require: true, message: "请输入项目详情!"}],
                            initialValue: this.state.project.detail
                        })(
                            <TextArea disabled={this.state.disabled} autosize={{ minRows: 3 }} />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="附件"
                    >
                        <span className="ant-form-text"><a href={this.state.project.accessory.link}>{this.state.project.accessory.name}</a>
                        </span>
                    </FormItem>
                    <FormItem wrapperCol={{ span: 8, offset: 3 }}>
                        {
                            !this.state.disabled ? <Button type="primary" onClick={this.handleSubmit} style={{"marginRight": "20px"}}>立即发布</Button> : ""
                        }
                        {
                            !this.state.disabled ? <Button type="danger" onClick={this.resetForm} style={{"marginRight": "20px"}}>重置</Button> : ""
                        }
                        <Button icon="rollback" onClick={this.return}>返回</Button>
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedDebtsForm = Form.create({})(DebtsForm);

module.exports = WrappedDebtsForm;


