import React,{Component} from 'react'
import { hashHistory } from 'react-router'
import { Form, Button, message, Radio, Spin, Input, Select, Cascader } from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;
const { TextArea } = Input;
import BenefitsItem from '../FormItem/benefitsItem.jsx'

export class StockForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            disabled: props.actionType == "detail" ? true : false,
            loading: false,
            "assetsType": {
                "debts":[],
                "stock": [],
                "purchase": [],
                "transfer": []
            },
            "industryList": [],
            "cascaderRegion": [],
            "project": {   //项目
                "key": "", // 编号
                "title": "", //标题
                "category": "", //类别
                "type": "", //类型
                "detail": "",   //详情
                "accessory": {        //附件
                    "name": "",
                    "link": ""
                }
            },
            "financing": {  //融资方
                "industry": "", //行业
                "aptitude": "", //资质
                "parentAptitued": "", //母公司资质
                "region": [], //地区
                "previousProfits": "", //去年净利润
                "recentProfits": "",
                "nextProfits": "",
                "number": "", //金额
                "deadline": "", //期限
                "maxRate": "" //最高可接受利率
            },
            "mortgage": {       //抵押物
                "isNeed": "",    //是否有抵押物
                "type": [],  //抵押物类型
                "name": "",  //抵押物名称
                "value": "",  //抵押物价值
                "rate": "" //抵押物折扣率
            },
            "guarantee": {
                "isNeed": "",    //是否必须担保
                "method": ""   //担保方式
            },
            "stock": {
                "type": "",  //参股性:1   控股型:2
                "property": "",  //老股转让:1   新股增发:2
                "number": "",  //转让金额
                "rate": "", //出让股权比例
                "estimate": {  //估值
                    "times": "",
                    "type": ""
                }
            },
            "target": {  //标的企业
                "name": "",
                "industry": "",
                "region": "",
                "value": "", //企业整体估值
                "previousProfits": "",
                "recentProfits": "",
                "nextProfits": ""
            },
            "assets": { //资产
                "region": "",
                "number": "",
                "value": ""
            }
        };
        ['handleSubmit', 'resetForm', 'stockPropertyChange'].forEach(m=>this[m] = this[m].bind(this))
    }

    stockPropertyChange(val) {
        Object.assign(this.state.stock, {property: val});
        this.setState({
            stock: this.state.stock
        })
    }

    componentWillMount() {
        let that = this;
        this.setState({loading: true});
        ajax({
            url: "/delegateProject/getInfoDetail",
            method: "post",
            data: {id: that.props.infoID ? that.props.infoID : ""},
            callback: function (data) {
                if (data.status == 200) {
                    let newData = data.data;
                    that.setState(Object.assign({loading: false}, newData));
                }
            }
        })
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if(!err) {
                ajax({
                    url: "/delegateProject/edit",
                    method: "post",
                    data: this.props.actionType == "edit" ? Object.assign(values, {id: this.props.infoID}) : values,
                    callback: function(data) {
                        if(data.status == 200) {
                            message.success("保存成功!", 1, function() {
                                hashHistory.push("/delegateProject")
                            });
                        } else {
                            message.error(data.errMsg, 1)
                        }
                    }
                });
            }
        })
    }

    return() {
        history.back();
    }

    resetForm() {
        this.props.form.resetFields();
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 14 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="项目标题"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("project.title", {
                            rules: [{required: true, message: "请输入标题!"}],
                            initialValue: this.state.project.title
                        })(
                            <Input disabled={this.state.disabled} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="项目类别"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("project.category", {
                            initialValue: "2"
                        })(
                            <Select disabled>
                                <Option value="1">债权</Option>
                                <Option value="2">股权</Option>
                                <Option value="3">资金收购</Option>
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="参/控股"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("stock.type", {
                            rules: [{
                                required: true,
                                message: "请选择类型!"
                            }],
                            initialValue: this.state.stock.type
                        })(
                            <RadioGroup disabled={this.state.disabled} >
                                <Radio value="1">参股型投资</Radio>
                                <Radio value="2">控股型投资</Radio>
                            </RadioGroup>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="项目类型"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("project.type", {
                            rules: [{
                                required: true,
                                message: "请选择项目类型!"
                            }],
                            initialValue: this.state.project.type
                        })(
                            <Select disabled={this.state.disabled}>
                                {
                                    this.state.assetsType.stock.map(function(item) {
                                        return <Option value={item.key} key={item.key}>{item.value}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="股权性质"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("stock.property", {
                            rules: [{
                                required: true,
                                message: "请选择股权性质"
                            }],
                            initialValue: this.state.stock.property
                        })(
                            <Select onChange={this.stockPropertyChange} disabled={this.state.disabled}>
                                <Option value="1">老股转让</Option>
                                <Option value="2">新股增发</Option>
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label={this.state.stock.property == "1" ? "转让方名称" : "融资方名称"}
                    >
                        <div>详见我的联系方式</div>
                    </FormItem>
                    {
                        this.state.stock.property == "1" ?
                            <FormItem
                                required
                                {...formItemLayout}
                                label= "标的企业名称"
                                hasFeedback={!this.state.disabled}
                            >
                                {getFieldDecorator("target.name", {
                                    rules: [{
                                        required: true,
                                        message: "请输入标的企业名称!"
                                    }],
                                    initialValue: this.state.target.name
                                })(
                                    <Input disabled={this.state.disabled} />
                                )}
                            </FormItem> : ""
                    }
                    <FormItem
                        required
                        {...formItemLayout}
                        label={this.state.stock.property == "1" ? "标的企业所属行业" : "融资方所属行业"}
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator(this.state.stock.property == "1" ? "target.industry" : "financing.industry", {
                            rules: [{
                                required: true,
                                message: "请设置所属行业!"
                            }],
                            initialValue: this.state.stock.property == "1" ? this.state.target.industry : this.state.financing.industry
                        })(
                            <Select disabled={this.state.disabled}>
                                {
                                    this.state.industryList.map(function(item) {
                                        return <Option value={item.key} key={item.key}>{item.value}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label={this.state.stock.property == "1" ? "标的企业所在地" : "融资方所在地"}
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator(this.state.stock.property == "1" ? "target.region" : "financing.region", {
                            rules: [{
                                required: true,
                                message: "请设置所在地!"
                            }],
                            initialValue: this.state.stock.property == "1" ? this.state.target.region : this.state.financing.region
                        })(
                            <Cascader disabled={this.state.disabled}
                                      options={this.state.cascaderRegion} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label={this.state.stock.property == "1" ? "标的企业去年利润" : "融资方去年利润"}
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator(this.state.stock.property == "1" ? "target.previousProfits" : "financing.previousProfits", {
                            rules: [{
                                required: true,
                                message: '请输入去年净利润!'
                            }],
                            initialValue:this.state.stock.property == "1" ? this.state.target.previousProfits : this.state.financing.previousProfits
                        })(
                            <Input disabled={this.state.disabled} style={{width: "93%"}} />
                        )}
                        <span className="ant-form-text" style={{marginLeft: 7}}>万元</span>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label={this.state.stock.property == "1" ? "转让金额" : "融资金额"}
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("stock.number", {
                            rules: [{
                                required: true,
                                message: "请输入金额!"
                            }],
                            initialValue: this.state.stock.number
                        })(
                            <Input disabled={this.state.disabled} style={{width: "93%"}} />
                        )}
                        <span className="ant-form-text" style={{marginLeft: 7}}>万元</span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="出让股权比例"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("stock.rate", {
                            initialValue: this.state.stock.rate
                        })(
                            <Input disabled={this.state.disabled} style={{width: "93%"}} />
                        )}
                        <span className="ant-form-text" style={{marginLeft: 7}}>%</span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="企业整体估值"
                    >
                        {getFieldDecorator(this.state.stock.property == "1" ? "target.number" : "financing.number", {
                            initialValue: this.state.stock.property == "1" ? this.state.target.number : this.state.financing.number
                        })(
                            <Input disabled={this.state.disabled} style={{width: "93%"}} />
                        )}
                        <span className="ant-form-text" style={{marginLeft: 7}}>万元</span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="投资估值倍数"
                    >
                        {getFieldDecorator("stock.estimate", {
                            initialValue: {
                                times: this.state.stock.estimate.times,
                                type: this.state.stock.estimate.type
                            }
                        })(<BenefitsItem disabled={this.state.disabled} />)}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="企业今年预计净利润"
                    >
                        {getFieldDecorator(this.state.stock.property == "1" ? "target.recentProfits" : "financing.recentProfits", {
                            initialValue: this.state.stock.property == "1" ? this.state.target.recentProfits : this.state.financing.recentProfits
                        })(
                            <Input disabled={this.state.disabled} style={{width: "93%"}} />
                        )}
                        <span className="ant-form-text" style={{marginLeft: 7}}>万元</span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="企业明年预计净利润"
                    >
                        {getFieldDecorator(this.state.stock.property == "1" ? "target.nextProfits" : "financing.nextProfits", {
                            initialValue: this.state.stock.property == "1" ? this.state.target.nextProfits: this.state.financing.nextProfits
                        })(
                            <Input disabled={this.state.disabled} style={{width: "93%"}} />
                        )}
                        <span className="ant-form-text" style={{marginLeft: 7}}>万元</span>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="项目详情"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("project.detail", {
                            rules: [{required: true, message: "请输入资金详情!"}],
                            initialValue: this.state.project.detail
                        })(
                            <TextArea disabled={this.state.disabled} autosize={{ minRows: 3 }} />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="附件"
                    >
                        <span className="ant-form-text"><a href={this.state.project.accessory.link}>{this.state.project.accessory.name}</a>
                        </span>
                    </FormItem>
                    <FormItem wrapperCol={{ span: 8, offset: 3 }}>
                        {
                            !this.state.disabled ? <Button type="primary" onClick={this.handleSubmit} style={{"marginRight": "20px"}}>立即发布</Button> : ""
                        }
                        {
                            !this.state.disabled ? <Button type="danger" onClick={this.resetForm} style={{"marginRight": "20px"}}>重置</Button> : ""
                        }
                        <Button icon="rollback" onClick={this.return}>返回</Button>
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedStockForm = Form.create({})(StockForm);

module.exports = WrappedStockForm;


